import React from 'react';

import './tracks.css';
import general from '../../images/general-color.svg';
import ai from '../../images/ai-color.svg';
import blockchain from '../../images/blockchain-color.svg';
import web from '../../images/webapp-color.svg';
import xr from '../../images/vr-color.svg';
import iot from '../../images/iot-color.svg';
import anime from 'animejs';


class tracks extends React.Component{
    state={
        items:[{item:blockchain, title:'BlockChain'},
        {item:ai, title:'AI'},
        {item:web, title:'Web'},
        {item:xr, title:'AR/VR'},
        {item:iot, title:'Iot'},
        {item:general, title:'General'}]
    }
    componentDidMount(){
        let hour = new Date().getHours();
        // hour = 21;
        if(hour >= 20 || hour <=6){
            this.setState({
                background:'trackContain contain Dark'
            })
        }
        if(hour >6 && hour <=14){
            this.setState({
                background:'trackContain contain color-morning'
            })
        }
        if(hour >14 && hour <20){
            this.setState({
                background:'trackContain contain color-evening'
            })
        }

            var textWrapper = document.querySelector('.track-heading');
            
                 textWrapper.innerHTML = textWrapper.textContent.replace(/\S/g, "<span class='letter'>$&</span>");
                 let count =1;
                 textWrapper.style.color ="transparent";
                 window.addEventListener('scroll', function(){
                    let positionTop = textWrapper.getBoundingClientRect().top;
                     if(positionTop - window.innerHeight <=0){
                            if(count === 1){
                                textWrapper.style.color ="white";
                                count = 0;
                                console.log('hi');
                                anime.timeline({loop: false})
                                .add({
                                  targets: '.track-heading .letter',
                                  scale: [0, 1],
                                  duration: 1500,
                                  elasticity: 600, 
                                  delay:200,
                                  direction:'normal'
                                })
                            }
                        
                     }
                     let elem = document.querySelectorAll('.in-left');
                     let elem1 = document.querySelectorAll('.in-right');
                     window.addEventListener('scroll', function(){
                         for(let i=0; i<elem.length; i++){
                             let positionTop = elem[i].getBoundingClientRect().top;
                             if(positionTop - window.innerHeight<=-10){
                                 elem[i].classList.add('come-in-left');
                             }
                         }
                     })
                     window.addEventListener('scroll', function(){
                        for(let i=0; i<elem1.length; i++){
                            let positionTop = elem1[i].getBoundingClientRect().top;
                            if(positionTop - window.innerHeight<=-10){
                                elem1[i].classList.add('come-in-right');
                            }
                        }
                    })

                    
                 })
                    
                           
     
    }
    render(){
        return(
            <div className={this.state.background}>
                
                    <div id="tracks">
            <h1 className="track-heading">TRACKS</h1>
                <div className="margin hidden">
            {/* {this.state.items.map(item => {
                return <div className="track" id={Math.random()}><img className="track-image" src={item.item} alt=""></img>
                <div className="track-title">{item.title}</div>
                </div>
            })} */}
            <div className="track in-left" id={Math.random()}><img className="track-image" src={blockchain} alt=""></img>
                <div className="track-title">BlockChain</div>
                </div>
                <div className="track in-right" id={Math.random()}><img className="track-image" src={ai} alt=""></img>
                <div className="track-title">AI</div>
                </div>
                <div className="track in-left" id={Math.random()}><img className="track-image" src={web} alt=""></img>
                <div className="track-title">Web</div>
                </div>
                <div className="track in-right" id={Math.random()}><img className="track-image" src={xr} alt=""></img>
                <div className="track-title">AR/VR</div>
                </div>
                <div className="track in-left" id={Math.random()}><img className="track-image" src={iot} alt=""></img>
                <div className="track-title">Iot</div>
                </div>
                <div className="track in-right" id={Math.random()}><img className="track-image" src={general} alt=""></img>
                <div className="track-title">General</div>
                </div>
               </div>
               
                
            </div>
            </div>
            
        )
    }
}

export default tracks;