import React from 'react';

import './background-mobile.css';

import bigmountain from '../../images/big_mountain.svg';
import Tree from '../../images/tall_tree.svg';
import mountainDay from '../../images/mountain_day.svg';
import mountainEve from '../../images/mountain_evening.svg';

class SceneMobile extends React.Component{
    state={
        mountain:null
    }
    componentDidMount(){
        let hour = new Date().getHours();
        if(hour >= 20 || hour <=6){
            this.setState({
                mountain:bigmountain,
                stars:true,
                background:'dark SceneHolderM',
                sun:false
            })
        }
        if(hour >6 && hour <=14){
            this.setState({
                mountain:mountainDay,
                stars:false,
                background:'day SceneHolderM',
                sun:true
            })
        }
        if(hour >14 && hour <20){
            this.setState({
                mountain:mountainEve,
                stars:true,
                background:'evening SceneHolderM',
                sun:false
            })
        }
    }
    render(){
        return(
            <div className="fixed">
                <div className={this.state.background}>
                <img id="bigMountainM" src={this.state.mountain} alt=""></img>
                <img id="mediumMountainM" src={this.state.mountain} alt=""></img>
                <img id="tallTree1M" src={Tree} alt=""></img>
                <img id="mediumTree1M" src={Tree} alt=""></img>
                <img id="tallTree2M" src={Tree} alt=""></img>
                <img id="mediumTree2M" src={Tree} alt=""></img>
                {(this.state.stars) ? <div id="starsM"></div> : null}
                {(this.state.sun) ? <div id="sunM"></div> : null}
                
                <div id="githubM">
                    <h3 id="github-textM">Hack On Hills</h3>
                    {/* <div id="logoContainM">
                    </div> */}
                </div>
                
        </div>
            </div>
        );
    }
}

export default SceneMobile;