import React from 'react';

// import './tracks.css';
import './sponsors.css';

import Github from '../../images/github_logo.png';
import CodingBlocks from '../../images/Coding_blocks.png';
import Simba from '../../images/simba.png';
import ebizOn from '../../images/ebiz.png';
import sketch from '../../images/sketch.png';
import gitlab from '../../images/gitlab.png';
import anime from 'animejs';



class sponsors extends React.Component{
    state={
        items:[CodingBlocks,Simba, ebizOn, sketch, gitlab]
    }
    componentDidMount(){
        let hour = new Date().getHours();
        // hour = 21;
        if(hour >= 20 || hour <=6){
            this.setState({
                background:'trackContain Dark'
            })
        }
        if(hour >6 && hour <=14){
            this.setState({
                background:'trackContain color-morning'
            })
        }
        if(hour >14 && hour <20){
            this.setState({
                background:'trackContain color-evening'
            })
        }

        var textWrapper = document.querySelector('.sponsor-heading');
            
                 textWrapper.innerHTML = textWrapper.textContent.replace(/\S/g, "<span class='letter'>$&</span>");
                 let count =1;
                 textWrapper.style.visibility ="hidden";
                 window.addEventListener('scroll', function(){
                    let positionTop = textWrapper.getBoundingClientRect().top;
                     if(positionTop - window.innerHeight <=-10){
                            if(count === 1){
                                textWrapper.style.visibility ="visible";
                                count = 0;
                                anime.timeline({loop: false})
                                .add({
                                  targets: '.sponsor-heading .letter',
                                  scale: [0, 1],
                                  duration: 500,
                                  elasticity: 600, 
                                  delay:200,
                                  direction:'normal'
                                })
                            }
                        
                     }
                    
                 })
    }
    render(){
        return(
            <div className={this.state.background}>
                
            <div className="tracks hidden">
            <h1 className="track-heading sponsor-heading">SPONSORS</h1>
                <div className="background-white">
                <h2 id="sponsorHeadContain"><img id="sponsorHead" src={Github} alt=""></img></h2>
                <div className="margin sponsorMargin">
                {this.state.items.map(item => {
                return <div className="sponsor" id={Math.random()}><img className="sponsor-image" src={item} alt=""></img></div>
            })}
                </div>
           
               </div>
                
            </div>
            </div>
            
        )
    }
}

export default sponsors;